* {
  margin: 0;
  padding: 0;
  list-style-type: none;
  box-sizing: border-box;
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contaiter {
  width: 100%;
  height: 100%;
  padding: 20px;
}

.clock-input {
  display: flex;
  gap: 20px;
  align-items: flex-end;
  justify-content: center;
  margin-bottom: 20px;
}

.input {
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 60px;
  background-color: white;
  font-size: 30px;
}

.clock-input-button {
  width: 200px;
  height: 60px;
  background-color: #ffc600;
  cursor: pointer;
  font-size: 30px;
}

.title {
  font-size: 30px;
  text-align: center;
}

.clock {
  position: relative;
  margin: 30px;
  width: 350px;
  height: 350px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("./img/clock.png"),
    radial-gradient(
      circle,
      rgb(37, 73, 90) 0%,
      rgb(25, 48, 60) 11%,
      rgba(9, 25, 33, 1) 38%
    );
  background-size: cover;
  border-radius: 50%;
  box-shadow: 0 -15px 15px rgba(134, 134, 134, 0.35), inset 0 -15px 15px #191919,
    0 15px 15px rgba(134, 134, 134, 0.35), inset 0 15px 15px #191919;
}

.clock:before {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  background-color: #ffffff;
  border-radius: 50%;
  z-index: 999;
}

.clock .hour,
.clock .min,
.clock .sec {
  position: absolute;
}

.clock .hour,
.hr {
  width: 160px;
  height: 160px;
}

.clock .min,
.mn {
  width: 190px;
  height: 190px;
}

.clock .sec,
.sc {
  width: 230px;
  height: 230px;
}

.hr,
.mn,
.sc {
  display: flex;
  justify-content: center;
  position: absolute;
  border-radius: 50%;
}

.hr:before {
  content: "";
  position: absolute;
  width: 8px;
  height: 80px;
  background-color: #ffc600;
  z-index: 10;
  border-radius: 6px 6px 0 0;
}

.mn:before {
  content: "";
  position: absolute;
  width: 4px;
  height: 90px;
  background-color: #fff;
  z-index: 11;
  border-radius: 6px 6px 0 0;
}

.sc:before {
  content: "";
  position: absolute;
  width: 2px;
  height: 150px;
  background-color: #fff;
  z-index: 12;
  border-radius: 6px 6px 0 0;
}

.clock-delete {
  background-color: transparent;
  border: none;
  position: absolute;
  top: -30px;
  right: 0;
  cursor: pointer;
}

ul {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.clock-title {
  position: absolute;
  top: -40px;
  font-size: 30px;
  color: rgb(8, 8, 48);
}
